import {useState, useRef, useEffect, ReactElement, useCallback} from 'react';
import styled, {css} from 'styled-components';
import {
  FaStreetView,
  FaUndo,
  FaRedo,
  FaSmile,
  FaMale,
  FaShoePrints,
  FaSave,
  FaTimes,
  FaDollarSign
} from 'react-icons/fa';
import {RiOpenArmLine} from 'react-icons/ri';
import {GiClothes} from 'react-icons/gi';

import ReactTooltip from 'react-tooltip';

import {CameraState, RotateState, SpecialState} from './interfaces';

interface ToggleButtonProps {
  active: boolean;
}

interface ToggleOptionProps {
  active: boolean;
  tooltip: string;
  onClick: () => void;
}

interface ExtendendContainerProps {
  width: number;
}

interface ExtendendOptionProps {
  icon: ReactElement;
}

interface OptionsProps {
  camera: CameraState;
  rotate: RotateState;
  special: SpecialState;
  handleSetCamera: (key: keyof CameraState) => void;
  handleTurnAround: () => void;
  handleRotateLeft: () => void;
  handleRotateRight: () => void;
  handleArmsUp: () => void;
  handleToggleClothes: () => void;
  handleSave: () => void;
  handleShop: () => void;
  handleExit: () => void;
  canExit: boolean;
  isShop: boolean;
  currentPrice: number;
  hideSave: boolean;
}

const Container = styled.div`
  height: 100vh;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  padding: 40px 0;

  > * {
    & + * {
      margin-top: 10px;
    }
  }
`;

const ToggleButton = styled.button<ToggleButtonProps>`
  height: 40px;
  width: 40px;

  display: flex;
  align-items: center;
  justify-content: center;

  border: 0;
  border-radius: 4px;

  box-shadow: 0px 0px 5px rgb(0, 0, 0, 0.2);

  transition: all 0.2s;

  color: rgba(255, 255, 255, 0.9);
  background: rgba(0, 0, 0, 0.9);

  &:hover {
    color: rgba(255, 255, 255, 1);
    background: rgba(0, 0, 0, 0.9);
  }

  &:active {
    transform: scale(0.8);
  }

  ${({active}) =>
  active &&
  css`
      color: rgba(0, 0, 0, 0.7);
      background: rgba(255, 255, 255);

      &:hover {
        color: rgba(0, 0, 0, 0.9);
        background: rgba(255, 255, 255, 1);
      }
    `}
`;

const Option = styled.button`
  height: 40px;
  width: 40px;

  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;

  flex-shrink: 0;

  border: 0;
  border-radius: 4px;

  box-shadow: 0px 0px 5px rgb(0, 0, 0, 0.2);

  transition: all 0.1s;

  color: rgba(255, 255, 255, 0.9);
  background: rgba(0, 0, 0, 0.9);

  &:hover {
    color: rgba(255, 255, 255, 1);
    background: rgba(0, 0, 0, 0.9);
  }

  &:active {
    transform: scale(0.8);
    color: rgba(0, 0, 0, 0.7);
    background: rgba(255, 255, 255);
  }
`;

const ExtendedContainer = styled.div<ExtendendContainerProps>`
  height: 40px;

  display: flex;
  align-items: flex-start;
  justify-content: flex-start;

  width: ${({width}) => `${width + 40}px`};

  transition: width 0.3s;

  overflow: hidden;
`;

const ExtendedIcon = styled.div`
  height: 40px;
  width: 40px;

  display: flex;
  align-items: center;
  justify-content: center;

  flex-shrink: 0;

  border: 0;
  border-radius: 4px;

  color: rgba(255, 255, 255, 0.9);
  background: rgba(0, 0, 0, 0.9);
`;

const ExtendedChildren = styled.div`
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;

  padding-left: 10px;

  > * {
    & + * {
      margin-left: 10px;
    }
  }
`;

const ToggleOption: React.FC<ToggleOptionProps> = ({children, active, tooltip, onClick}) => {
  return (
    <ToggleButton data-tip={tooltip} data-for="textInfo" type="button" active={active} onClick={onClick}>
      {children}
    </ToggleButton>
  );
};

const ExtendedOption: React.FC<ExtendendOptionProps> = ({children, icon}) => {
  const [extended, setExtended] = useState(true);

  const [width, setWidth] = useState(0);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (ref.current) {
      setWidth(ref.current.offsetWidth);
      setExtended(false);
    }
  }, [ref, setWidth]);

  const handleMouseEnter = useCallback(() => {
    setExtended(true);
  }, [setExtended]);

  const handleMouseLeave = useCallback(() => {
    setExtended(false);
  }, [setExtended]);

  return (
    <ExtendedContainer width={extended ? width : 0} onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
      <ExtendedChildren ref={ref}>{children}</ExtendedChildren>
      <ExtendedIcon>{icon}</ExtendedIcon>
    </ExtendedContainer>
  );
};

const formatter = new Intl.NumberFormat('en-US', {
  style: 'currency',
  currency: 'USD',
  maximumFractionDigits: 0
});

const Options: React.FC<OptionsProps> = ({
                                           camera,
                                           rotate,
                                           special,
                                           handleSetCamera,
                                           handleTurnAround,
                                           handleRotateLeft,
                                           handleRotateRight,
                                           handleArmsUp,
                                           handleToggleClothes,
                                           handleExit,
                                           handleSave,
                                           handleShop,
                                           canExit,
                                           isShop,
                                           currentPrice,
                                           hideSave,
                                         }) => {
  return (
    <Container>
      <ReactTooltip id="textInfo" place="left" type="dark" backgroundColor="rgba(0, 0, 0, 0.9)"
                    textColor="rgba(255, 255, 255, 0.9)" effect="solid"/>
      <ToggleOption active={camera.head} onClick={() => handleSetCamera('head')} tooltip="Pohled na hlavu">
        <FaSmile size={20}/>
      </ToggleOption>
      <ToggleOption active={camera.body} onClick={() => handleSetCamera('body')} tooltip="Pohled na hruď">
        <FaMale size={20}/>
      </ToggleOption>
      <ToggleOption active={camera.bottom} onClick={() => handleSetCamera('bottom')} tooltip="Pohled na nohy">
        <FaShoePrints size={20}/>
      </ToggleOption>
      <ToggleOption active={rotate.back} onClick={handleTurnAround} tooltip="Otočení">
        <FaStreetView size={20}/>
      </ToggleOption>
      <ToggleOption active={rotate.left} onClick={handleRotateLeft} tooltip="Natočení doleva">
        <FaRedo size={20}/>
      </ToggleOption>
      <ToggleOption active={rotate.right} onClick={handleRotateRight} tooltip="Natočení doprava">
        <FaUndo size={20}/>
      </ToggleOption>
      <ToggleOption active={special.handsUp} onClick={handleArmsUp} tooltip="Ruce nahoru">
        <RiOpenArmLine size={20}/>
      </ToggleOption>
      <ToggleOption active={special.clothesOff} onClick={handleToggleClothes} tooltip="Skrýt oblečení">
        <GiClothes size={20}/>
      </ToggleOption>
      {!hideSave && !isShop && (
        <ToggleOption onClick={handleSave} active={false} tooltip="Uložit">
          <ReactTooltip id="textInfo" place="left" type="dark" backgroundColor="rgba(0, 0, 0, 0.9)"
                        textColor="rgba(255, 255, 255, 0.9)" effect="solid"/>
          <FaSave size={20}/>
        </ToggleOption>
      )}
      {!hideSave && isShop && currentPrice > 0 && (
        <ToggleOption onClick={handleShop} active={false} tooltip={"Zakoupit (" + formatter.format(currentPrice) + ")"}>
          <ReactTooltip id="textInfo" place="left" type="dark" backgroundColor="rgba(0, 0, 0, 0.9)"
                        textColor="rgba(255, 255, 255, 0.9)" effect="solid"/>
          <FaDollarSign size={20}/>
        </ToggleOption>
      )}
      {canExit && (
        <ToggleOption onClick={handleExit} active={false} tooltip="Zrušit">
          <ReactTooltip id="textInfo" place="left" type="dark" backgroundColor="rgba(0, 0, 0, 0.9)"
                        textColor="rgba(255, 255, 255, 0.9)" effect="solid"/>
          <FaTimes size={20}/>
        </ToggleOption>
      )}
    </Container>
  );
};

export default Options;
