interface tattoosProps {
  tattooList: Array<any>;
  currentTattoos: Array<any>;
  handleTattooChange: (selectedItems: Array<any>) => void;
}

const Tattoos = ({tattooList, currentTattoos, handleTattooChange}: tattoosProps) => {
  const {locales} = useNuiState();

  if (!locales) {
    return null;
  }

  return (
    <Section title="Tetování">
      <Item>
        <Container>
          <MultiSelect
            items={tattooList}
            withGrouping
            showSelectAll={false}
            showSelectedItems={false}
            selectedItems={currentTattoos}
            onChange={handleTattooChange}
            messages={{
              searchPlaceholder: "Hledat",
              noItemsMessage: "Nebylo nic nalezeno...",
              noneSelectedMessage: "Žádný výběr",
              selectedMessage: "vybráno",
              selectAllMessage: "Vybrat vše",
              clearAllMessage: "Zrušit vše",
            }}
          />
        </Container>
      </Item>
    </Section>
  );
};

export default Tattoos;
