import {useState, useEffect, useCallback, useMemo} from 'react';
import {useTransition, animated} from 'react-spring';
import {useNuiState} from '../../hooks/nuiState';
import Nui from '../../Nui';
import mock from '../../mock';

import {
  CustomizationConfig,
  PedAppearance,
  AppearanceSettings,
  PedHeadBlend,
  PedFaceFeatures,
  PedHeadOverlays,
  PedHeadOverlayValue,
  PedHair,
  CameraState,
  vMenuState,
  pedChangeState,
  TattoosState,
  TattoosSelect,
  HairAndTattoo,
  AppearanceSave,
  ShopData
} from './interfaces';

import {
  APPEARANCE_INITIAL_STATE,
  SETTINGS_INITIAL_STATE,
  CAMERA_INITIAL_STATE,
  ROTATE_INITIAL_STATE,
  SPECIAL_INITIAL_STATE,
} from './settings';

import VMenuPed from './vMenuPed';
import Ped from './Ped';
import HeadBlend from './HeadBlend';
import FaceFeatures from './FaceFeatures';
import HeadOverlays from './HeadOverlays';
import Components from './Components';
import Props from './Props';
import Options from './Options';
import Modal from '../Modal';

import {Wrapper, Container} from './styles';
import Tattoos from "./Tattoos";

const exp = (global as any).exports;

if (process.env.REACT_APP_ENV !== 'production') {
  mock('appearance_get_settings_and_data', () => ({
    appearanceData: {...APPEARANCE_INITIAL_STATE, model: 'mp_f_freemode_01'},
    appearanceSettings: {
      ...SETTINGS_INITIAL_STATE,
      eyeColor: {min: 0, max: 24},
      hair: {
        ...SETTINGS_INITIAL_STATE.hair,
        color: {
          items: [
            [255, 0, 0],
            [0, 255, 0],
            [0, 0, 255],
            [0, 0, 255],
          ],
        },
      },
    },
  }));

  mock('appearance_change_model', () => SETTINGS_INITIAL_STATE);

  mock('appearance_change_component', () => SETTINGS_INITIAL_STATE.components);

  mock('appearance_change_prop', () => SETTINGS_INITIAL_STATE.props);
}

const Appearance = () => {
  const [config, setConfig] = useState<CustomizationConfig>();
  const [vMenu, setvMenu] = useState<vMenuState>();
  const [pedChange, setPedChange] = useState<pedChangeState>();
  const [tattoos, setTattoos] = useState<TattoosState>();
  const [shopData, setShopData] = useState<ShopData>();

  const [data, setData] = useState<PedAppearance>();
  const [storedData, setStoredData] = useState<PedAppearance>();
  const [appearanceSettings, setAppearanceSettings] = useState<AppearanceSettings>();

  const [camera, setCamera] = useState(CAMERA_INITIAL_STATE);
  const [rotate, setRotate] = useState(ROTATE_INITIAL_STATE);
  const [special, setSpecial] = useState(SPECIAL_INITIAL_STATE);

  const [saveModal, setSaveModal] = useState(false);
  const [exitModal, setExitModal] = useState(false);
  const [shopModal, setShopModal] = useState(false);
  const [shopNotEnoughModal, setShopNotEnoughModal] = useState(false);
  const [vMenuPedModal, setvMenuPedModal] = useState(false);
  const [pedModal, setPedModal] = useState(false);

  const {display, setDisplay, locales, setLocales} = useNuiState();

  const wrapperTransition = useTransition(display.appearance, null, {
    from: {transform: 'translateX(-50px)', opacity: 0},
    enter: {transform: 'translateY(0)', opacity: 1},
    leave: {transform: 'translateX(-50px)', opacity: 0},
  });

  const saveModalTransition = useTransition(saveModal, null, {
    from: {opacity: 0},
    enter: {opacity: 1},
    leave: {opacity: 0},
  });

  const exitModalTransition = useTransition(exitModal, null, {
    from: {opacity: 0},
    enter: {opacity: 1},
    leave: {opacity: 0},
  });

  const vMenuPedModalTransition = useTransition(vMenuPedModal, null, {
    from: {opacity: 0},
    enter: {opacity: 1},
    leave: {opacity: 0},
  });

  const pedModalTransition = useTransition(pedModal, null, {
    from: {opacity: 0},
    enter: {opacity: 1},
    leave: {opacity: 0},
  });

  const shopModalTransition = useTransition(shopModal, null, {
    from: {opacity: 0},
    enter: {opacity: 1},
    leave: {opacity: 0},
  });

  const shopNotEnoughModalTransition = useTransition(shopNotEnoughModal, null, {
    from: {opacity: 0},
    enter: {opacity: 1},
    leave: {opacity: 0},
  });

  const handleTurnAround = useCallback(async () => {
    const turnResult = await Nui.post('appearance_turn_around');
    if (turnResult) {
      setRotate({left: rotate.left, right: rotate.right, back: !rotate.back});
    }
  }, [setRotate, rotate]);

  const handleSetCamera = useCallback(
    (key: keyof CameraState) => {
      setCamera({...CAMERA_INITIAL_STATE, [key]: !camera[key]});
      setRotate(ROTATE_INITIAL_STATE);

      if (!camera[key]) {
        Nui.post('appearance_set_camera', key);
      } else {
        Nui.post('appearance_set_camera', 'default');
      }
    },
    [camera, setCamera, setRotate],
  );

  const handleRotateLeft = useCallback(() => {
    setRotate({left: !rotate.left, right: false, back: rotate.back});

    if (!rotate.left) {
      Nui.post('appearance_rotate_camera', 'left');
    } else {
      Nui.post('appearance_set_camera', 'current');
    }
  }, [setRotate, rotate]);

  const handleRotateRight = useCallback(() => {
    setRotate({left: false, right: !rotate.right, back: rotate.back});

    if (!rotate.right) {
      Nui.post('appearance_rotate_camera', 'right');
    } else {
      Nui.post('appearance_set_camera', 'current');
    }
  }, [setRotate, rotate]);

  const handleArmsUp = useCallback(() => {
    setSpecial({handsUp: !special.handsUp, clothesOff: special.clothesOff});

    Nui.post('appearance_hands', !special.handsUp);
  }, [setSpecial, special]);

  const handleToggleClothes = useCallback(
    async () => {
      if (data && appearanceSettings) {
        const clothesOff = !special.clothesOff;

        if (clothesOff) {
          await Nui.post('appearance_naked', data.model);
        } else {
          await Nui.post('appearance_change_components', data.components);
          await Nui.post('appearance_change_props', data.props);
        }

        setSpecial({handsUp: special.handsUp, clothesOff});
      }
    }, [setSpecial, special, data, data],
  );

  const handleSaveModal = useCallback(() => {
    setSaveModal(true);
  }, [setSaveModal]);

  const handleShopModal = useCallback(async () => {
    const {currentPrice, currentCash} = await Nui.post('appearance_shop_get_price');
    setShopData({currentPrice, currentCash});
    if (currentPrice > currentCash) {
      setShopNotEnoughModal(true);
    } else {
      setShopModal(true);
    }
  }, [setShopModal, setShopNotEnoughModal, setShopData]);

  const handleNotEnoughShopMenu = useCallback(
    async (accept: boolean) => {
      setShopNotEnoughModal(false);
    },
    [setShopNotEnoughModal],
  );

  const handleShopMenu = useCallback(
    async (accept: boolean) => {
      if (accept && tattoos && shopData) {
        const appearanceSave: AppearanceSave = {
          appearance: data,
          selectedTattoos: tattoos.currentTattoos,
          currentPrice: shopData.currentPrice
        }
        await Nui.post('appearance_save', appearanceSave);
        setShopModal(false);
      } else {
        setShopModal(false);
      }
    },
    [setShopModal, data, tattoos, shopData],
  );

  const handleExitModal = useCallback(() => {
    setExitModal(true);
  }, [setExitModal]);

  const handleSave = useCallback(
    async (accept: boolean) => {
      if (accept && tattoos && shopData) {
        const appearanceSave: AppearanceSave = {appearance: data, selectedTattoos: tattoos.currentTattoos}
        await Nui.post('appearance_save', appearanceSave);
        setSaveModal(false);
      } else {
        setSaveModal(false);
      }
    },
    [setSaveModal, data, tattoos, shopData],
  );

  const handleExit = useCallback(
    async (accept: boolean) => {
      if (accept) {
        await Nui.post('appearance_exit');
        setExitModal(false);
      } else {
        setExitModal(false);
      }
    },
    [setExitModal],
  );

  const handlevMenu = useCallback(
    async (accept: boolean) => {
      if (accept && vMenu) {
        await Nui.post('set_vmenu_ped', vMenu.vMenuPedNames.indexOf(vMenu.vMenuPedSelected) + 1);

        const {appearanceSettings: _appearanceSettings, appearanceData} = await Nui.post('appearance_change_load');

        const {
          allTattoos,
          currentTattoos,
        } = await Nui.post('appearance_getTattoosInfo', appearanceData.model);
        setTattoos({
          allTattoos,
          currentTattoos,
        });
        setAppearanceSettings(_appearanceSettings);
        setData(appearanceData);

        const hairAndTattoo: HairAndTattoo = {hair: appearanceData.hair, selectedTattoos: currentTattoos}
        await Nui.post('appearance_change_hair_and_tattoos', hairAndTattoo);
      }
      setvMenuPedModal(false);
    },
    [vMenu, setvMenuPedModal, setData, setAppearanceSettings, setTattoos],
  );

  const handlevMenuPedImport = useCallback(
    async (value: string, peds: Array<string>) => {
      setvMenu({
        hasvMenuPeds: true,
        vMenuPedNames: peds,
        vMenuPedSelected: value,
      });
      setvMenuPedModal(true);
    },
    [setvMenu, setvMenuPedModal],
  );

  const handleModelChange = useCallback(
    async (value: string) => {
      setPedChange({
        pedToChange: value,
      });
      setPedModal(true);
    },
    [setPedModal, setPedChange],
  );

  const handlePedMenu = useCallback(
    async (accept: boolean) => {
      if (accept && pedChange && tattoos) {
        const {appearanceSettings: _appearanceSettings, appearanceData} = await Nui.post(
          'appearance_change_model',
          pedChange.pedToChange,
        );

        setAppearanceSettings(_appearanceSettings);
        setData(appearanceData);

        const {
          allTattoos,
          currentTattoos,
        } = await Nui.post('appearance_getTattoosInfo', pedChange.pedToChange);
        setTattoos({
          allTattoos,
          currentTattoos,
        });
      }
      setPedModal(false);
    },
    [setPedModal, setData, setAppearanceSettings, pedChange, tattoos, setTattoos],
  );

  const handleHeadBlendChange = useCallback(
    (key: keyof PedHeadBlend, value: number) => {
      if (!data) return;

      const updatedHeadBlend = {...data.headBlend, [key]: value};

      const updatedData = {...data, headBlend: updatedHeadBlend};

      setData(updatedData);

      Nui.post('appearance_change_head_blend', updatedHeadBlend);
    },
    [data, setData],
  );

  const handleFaceFeatureChange = useCallback(
    (key: keyof PedFaceFeatures, value: number) => {
      if (!data) return;

      const updatedFaceFeatures = {...data.faceFeatures, [key]: value};

      const updatedData = {...data, faceFeatures: updatedFaceFeatures};

      setData(updatedData);

      Nui.post('appearance_change_face_feature', updatedFaceFeatures);
    },
    [data, setData],
  );

  const handleHairChange = useCallback(
    async (key: keyof PedHair, value: number) => {
      if (!data || !appearanceSettings || !tattoos || !config) return;

      const updatedHair = {...data.hair, [key]: value};

      if (data.hair.style !== updatedHair.style) {
        updatedHair.texture = 0;
      }

      const updatedData = {...data, hair: updatedHair};

      setData(updatedData);

      const hairAndTattoo: HairAndTattoo = {hair: updatedHair, selectedTattoos: tattoos.currentTattoos}
      const updatedComponentSettings = await Nui.post('appearance_change_hair_and_tattoos', hairAndTattoo);

      const filteredComponentsSettings = appearanceSettings.components.filter(c => c.component_id !== 2);

      const updatedComponentsSettings = [...filteredComponentsSettings, updatedComponentSettings];

      const updatedSettings = {...appearanceSettings, components: updatedComponentsSettings};

      setAppearanceSettings(updatedSettings);

      if (config.isShop) {
        const {currentPrice, currentCash} = await Nui.post('appearance_shop_get_price');
        setShopData({currentPrice, currentCash});
      }
    },
    [data, setData, tattoos, config, setShopData],
  );

  const handleHeadOverlayChange = useCallback(
    async (key: keyof PedHeadOverlays, option: keyof PedHeadOverlayValue, value: number) => {
      if (!data || !config) return;

      const updatedValue = {...data.headOverlays[key], [option]: value};

      const updatedData = {...data, headOverlays: {...data.headOverlays, [key]: updatedValue}};

      setData(updatedData);

      Nui.post('appearance_change_head_overlay', {...data.headOverlays, [key]: updatedValue});

      if (config.isShop) {
        const {currentPrice, currentCash} = await Nui.post('appearance_shop_get_price');
        setShopData({currentPrice, currentCash});
      }
    },
    [data, setData, config, setShopData],
  );

  const handleEyeColorChange = useCallback(
    (value: number) => {
      if (!data) return;

      const updatedData = {...data, eyeColor: value};

      setData(updatedData);

      Nui.post('appearance_change_eye_color', value);
    },
    [data, setData],
  );

  const handleComponentDrawableChange = useCallback(
    async (component_id: number, drawable: number) => {
      if (!data || !appearanceSettings || !config) return;

      const component = data.components.find(c => c.component_id === component_id);

      if (!component) return;

      const updatedComponent = {...component, drawable, texture: 0};

      const filteredComponents = data.components.filter(c => c.component_id !== component_id);

      const updatedComponents = [...filteredComponents, updatedComponent];

      const updatedData = {...data, components: updatedComponents};

      setData(updatedData);

      const updatedComponentSettings = await Nui.post('appearance_change_component', updatedComponent);

      const filteredComponentsSettings = appearanceSettings.components.filter(c => c.component_id !== component_id);

      const updatedComponentsSettings = [...filteredComponentsSettings, updatedComponentSettings];

      const updatedSettings = {...appearanceSettings, components: updatedComponentsSettings};

      setAppearanceSettings(updatedSettings);

      if (config.isShop) {
        const {currentPrice, currentCash} = await Nui.post('appearance_shop_get_price');
        setShopData({currentPrice, currentCash});
      }
    },
    [data, setData, appearanceSettings, setAppearanceSettings, config, setShopData],
  );

  const handleComponentTextureChange = useCallback(
    async (component_id: number, texture: number) => {
      if (!data || !appearanceSettings || !config) return;

      const component = data.components.find(c => c.component_id === component_id);

      if (!component) return;

      const updatedComponent = {...component, texture};

      const filteredComponents = data.components.filter(c => c.component_id !== component_id);

      const updatedComponents = [...filteredComponents, updatedComponent];

      const updatedData = {...data, components: updatedComponents};

      setData(updatedData);

      const updatedComponentSettings = await Nui.post('appearance_change_component', updatedComponent);

      const filteredComponentsSettings = appearanceSettings.components.filter(c => c.component_id !== component_id);

      const updatedComponentsSettings = [...filteredComponentsSettings, updatedComponentSettings];

      const updatedSettings = {...appearanceSettings, components: updatedComponentsSettings};

      setAppearanceSettings(updatedSettings);

      if (config.isShop) {
        const {currentPrice, currentCash} = await Nui.post('appearance_shop_get_price');
        setShopData({currentPrice, currentCash});
      }
    },
    [data, setData, appearanceSettings, setAppearanceSettings, config, setShopData],
  );

  const handlePropDrawableChange = useCallback(
    async (prop_id: number, drawable: number) => {
      if (!data || !appearanceSettings || !config) return;

      const prop = data.props.find(p => p.prop_id === prop_id);

      if (!prop) return;

      const updatedProp = {...prop, drawable, texture: 0};

      const filteredProps = data.props.filter(p => p.prop_id !== prop_id);

      const updatedProps = [...filteredProps, updatedProp];

      const updatedData = {...data, props: updatedProps};

      setData(updatedData);

      const updatedPropSettings = await Nui.post('appearance_change_prop', updatedProp);

      const filteredPropsSettings = appearanceSettings.props.filter(c => c.prop_id !== prop_id);

      const updatedPropsSettings = [...filteredPropsSettings, updatedPropSettings];

      const updatedSettings = {...appearanceSettings, props: updatedPropsSettings};

      setAppearanceSettings(updatedSettings);

      if (config.isShop) {
        const {currentPrice, currentCash} = await Nui.post('appearance_shop_get_price');
        setShopData({currentPrice, currentCash});
      }
    },
    [data, setData, appearanceSettings, setAppearanceSettings, config, setShopData],
  );

  const handlePropTextureChange = useCallback(
    async (prop_id: number, texture: number) => {
      if (!data || !appearanceSettings || !config) return;

      const prop = data.props.find(p => p.prop_id === prop_id);

      if (!prop) return;

      const updatedProp = {...prop, texture};

      const filteredProps = data.props.filter(p => p.prop_id !== prop_id);

      const updatedProps = [...filteredProps, updatedProp];

      const updatedData = {...data, props: updatedProps};

      setData(updatedData);

      const updatedPropSettings = await Nui.post('appearance_change_prop', updatedProp);

      const filteredPropsSettings = appearanceSettings.props.filter(c => c.prop_id !== prop_id);

      const updatedPropsSettings = [...filteredPropsSettings, updatedPropSettings];

      const updatedSettings = {...appearanceSettings, props: updatedPropsSettings};

      setAppearanceSettings(updatedSettings);

      if (config.isShop) {
        const {currentPrice, currentCash} = await Nui.post('appearance_shop_get_price');
        setShopData({currentPrice, currentCash});
      }
    },
    [data, setData, appearanceSettings, setAppearanceSettings, config, setShopData],
  );

  const handleTattooChange = useCallback(
    async (selectedTattoos: TattoosSelect[]) => {
      if (tattoos && data && config) {
        const hairAndTattoo: HairAndTattoo = {hair: data.hair, selectedTattoos}
        await Nui.post('appearance_change_hair_and_tattoos', hairAndTattoo);

        setTattoos({allTattoos: tattoos.allTattoos, currentTattoos: selectedTattoos});

        if (config.isShop) {
          const {currentPrice, currentCash} = await Nui.post('appearance_shop_get_price');
          setShopData({currentPrice, currentCash});
        }
      }
    },
    [setTattoos, setShopData, tattoos, data, config],
  );

  const isPedFreemodeModel = useMemo(() => {
    if (!data) return;

    return data.model === 'mp_m_freemode_01' || data.model === 'mp_f_freemode_01';
  }, [data]);

  useEffect(() => {
    Nui.post('appearance_get_locales').then(result => setLocales(JSON.parse(result)));

    Nui.onEvent('appearance_display', () => {
      setDisplay({appearance: true});
    });

    Nui.onEvent('appearance_hide', () => {
      setDisplay({appearance: false});
      setData(APPEARANCE_INITIAL_STATE);
      setStoredData(APPEARANCE_INITIAL_STATE);
      setAppearanceSettings(SETTINGS_INITIAL_STATE);
      setCamera(CAMERA_INITIAL_STATE);
      setRotate(ROTATE_INITIAL_STATE);
    });
  }, []);

  useEffect(() => {
    if (display.appearance) {
      (async () => {
        const {
          config: _config,
          appearanceSettings: settings,
          appearanceData,
        } = await Nui.post('appearance_get_settings_and_data');
        setConfig(_config);
        setAppearanceSettings(settings);
        setStoredData(appearanceData);
        setData(appearanceData);

        setSpecial({
          clothesOff: false,
          handsUp: false,
        });

        const hasvMenuPeds = await Nui.post('has_vmenu_peds');
        const vMenuPedNames = await Nui.post('get_vmenu_ped_names');
        const vMenuPedSelected = '';

        setvMenu({
          hasvMenuPeds,
          vMenuPedNames,
          vMenuPedSelected,
        });

        setShopData({
          currentPrice: 0,
          currentCash: 0,
        })

        const {
          allTattoos,
          currentTattoos,
        } = await Nui.post('appearance_getTattoosInfo', appearanceData.model);
        setTattoos({
          allTattoos,
          currentTattoos,
        });
      })();
    }
  }, [display.appearance]);

  if (!display.appearance || !config || !tattoos || !vMenu || !appearanceSettings || !data || !storedData || !locales || !special || !shopData) {
    return null;
  }

  const tattoosSelectList: TattoosSelect[] = [];
  const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
    maximumFractionDigits: 0
  });

  Object.entries(tattoos.allTattoos).forEach(
    ([zone, zoneTattoos]) => {
      Object.entries(zoneTattoos).forEach(
        ([_, tattoo]) => {
          const tattoId = tattoo.collection + "-" + tattoo.name;
          let label = tattoo.label;

          if (config.isShop) {
            label += ' (' + formatter.format(tattoo.price) + ')';
          }

          tattoosSelectList.push({
            id: tattoId,
            label: label,
            group: zone,
            collection: tattoo.collection,
            name: tattoo.name,
          });
        }
      );
    }
  );

  return (
    <>
      {wrapperTransition.map(
        ({item, key, props: style}) =>
          item && (
            <animated.div key={key} style={style}>
              <Wrapper>
                <Options
                  camera={camera}
                  rotate={rotate}
                  special={special}
                  handleSetCamera={handleSetCamera}
                  handleTurnAround={handleTurnAround}
                  handleRotateLeft={handleRotateLeft}
                  handleRotateRight={handleRotateRight}
                  handleArmsUp={handleArmsUp}
                  handleToggleClothes={handleToggleClothes}
                  handleSave={handleSaveModal}
                  handleShop={handleShopModal}
                  handleExit={handleExitModal}
                  canExit={config.canLeave}
                  isShop={config.isShop}
                  currentPrice={shopData.currentPrice}
                  hideSave={special.clothesOff}
                />
                <Container>
                  {!special.clothesOff && config.vMenuPedsImportAllow && vMenu.hasvMenuPeds && (
                    <VMenuPed
                      peds={vMenu.vMenuPedNames}
                      selectedPed={vMenu.vMenuPedSelected}
                      handlevMenuPedImport={handlevMenuPedImport}
                    />
                  )}
                  {!special.clothesOff && config.ped && (
                    <Ped
                      settings={appearanceSettings.ped}
                      storedData={storedData.model}
                      data={data.model}
                      handleModelChange={handleModelChange}
                    />
                  )}
                  {appearanceSettings && (
                    <>
                      {isPedFreemodeModel && config.headBlend && (
                        <HeadBlend
                          settings={appearanceSettings.headBlend}
                          storedData={storedData.headBlend}
                          data={data.headBlend}
                          handleHeadBlendChange={handleHeadBlendChange}
                        />
                      )}
                      {isPedFreemodeModel && config.faceFeatures && (
                        <FaceFeatures
                          settings={appearanceSettings.faceFeatures}
                          storedData={storedData.faceFeatures}
                          data={data.faceFeatures}
                          handleFaceFeatureChange={handleFaceFeatureChange}
                        />
                      )}
                      {config.headOverlays && (
                        <HeadOverlays
                          appearanceSettings={appearanceSettings.components}
                          settings={{
                            hair: appearanceSettings.hair,
                            headOverlays: appearanceSettings.headOverlays,
                            eyeColor: appearanceSettings.eyeColor,
                          }}
                          storedData={{
                            hair: storedData.hair,
                            headOverlays: storedData.headOverlays,
                            eyeColor: storedData.eyeColor,
                          }}
                          data={{
                            hair: data.hair,
                            headOverlays: data.headOverlays,
                            eyeColor: data.eyeColor,
                          }}
                          handleHairChange={handleHairChange}
                          handleHeadOverlayChange={handleHeadOverlayChange}
                          handleEyeColorChange={handleEyeColorChange}
                        />
                      )}
                    </>
                  )}
                  {!special.clothesOff && (config.components || config.mask) && (
                    <Components
                      settings={appearanceSettings.components}
                      isPedFreemodeModel={isPedFreemodeModel ?? false}
                      components={config.components}
                      mask={config.mask}
                      data={data.components}
                      storedData={storedData.components}
                      handleComponentDrawableChange={handleComponentDrawableChange}
                      handleComponentTextureChange={handleComponentTextureChange}
                    />
                  )}
                  {!special.clothesOff && config.props && (
                    <Props
                      settings={appearanceSettings.props}
                      data={data.props}
                      storedData={storedData.props}
                      handlePropDrawableChange={handlePropDrawableChange}
                      handlePropTextureChange={handlePropTextureChange}
                    />
                  )}
                  {config.tattoos && isPedFreemodeModel && (
                    <Tattoos
                      tattooList={tattoosSelectList}
                      currentTattoos={tattoos.currentTattoos}
                      handleTattooChange={handleTattooChange}
                    />
                  )}
                </Container>
              </Wrapper>
            </animated.div>
          ),
      )}
      {saveModalTransition.map(
        ({item, key, props: style}) =>
          item && (
            <animated.div key={key} style={style}>
              <Modal
                title={locales.modal.save.title}
                description={locales.modal.save.description}
                accept={locales.modal.accept}
                decline={locales.modal.decline}
                handleAccept={() => handleSave(true)}
                handleDecline={() => handleSave(false)}
              />
            </animated.div>
          ),
      )}
      {exitModalTransition.map(
        ({item, key, props: style}) =>
          item && (
            <animated.div key={key} style={style}>
              <Modal
                title={locales.modal.exit.title}
                description={locales.modal.exit.description}
                accept={locales.modal.accept}
                decline={locales.modal.decline}
                handleAccept={() => handleExit(true)}
                handleDecline={() => handleExit(false)}
              />
            </animated.div>
          ),
      )}
      {vMenuPedModalTransition.map(
        ({item, key, props: style}) =>
          item && (
            <animated.div key={key} style={style}>
              <Modal
                title="Importovat vMenu ped"
                description="Přejete si importovat vMenu ped a přepsat tím tak aktuální?"
                accept={locales.modal.accept}
                decline={locales.modal.decline}
                handleAccept={() => handlevMenu(true)}
                handleDecline={() => handlevMenu(false)}
              />
            </animated.div>
          ),
      )}
      {pedModalTransition.map(
        ({item, key, props: style}) =>
          item && (
            <animated.div key={key} style={style}>
              <Modal
                title="Změna pedu"
                description="Změnou pedu dojde k resetu oblečení, přejete si pokračovat?"
                accept={locales.modal.accept}
                decline={locales.modal.decline}
                handleAccept={() => handlePedMenu(true)}
                handleDecline={() => handlePedMenu(false)}
              />
            </animated.div>
          ),
      )}
      {shopModalTransition.map(
        ({item, key, props: style}) =>
          item && (
            <animated.div key={key} style={style}>
              <Modal
                title="Zakoupení"
                description={"Změna tě bude stát " + formatter.format(shopData.currentPrice) + ". Přeješ si pokračovat?"}
                accept={locales.modal.accept}
                decline={locales.modal.decline}
                handleAccept={() => handleShopMenu(true)}
                handleDecline={() => handleShopMenu(false)}
              />
            </animated.div>
          ),
      )}
      {shopNotEnoughModalTransition.map(
        ({item, key, props: style}) =>
          item && (
            <animated.div key={key} style={style}>
              <Modal
                title="Nedostatek hotovosti"
                description={"Potřebuješ " + formatter.format(shopData.currentPrice) + " na zaplacení! Máš u sebe pouze " + formatter.format(shopData.currentCash) + "."}
                accept="Zrušit"
                decline=""
                handleAccept={() => handleNotEnoughShopMenu(true)}
                handleDecline={() => handleNotEnoughShopMenu(false)}
              />
            </animated.div>
          ),
      )}
    </>
  );
};

export default Appearance;
