import { useNuiState } from '../../hooks/nuiState';

import Section from './components/Section';
import Item from './components/Item';
import SelectInput from './components/SelectInput';

interface vMenuPedProps {
  peds: Array<string>;
  selectedPed: string;
  handlevMenuPedImport: (value: string, peds: Array<string>) => void;
}

const VMenuPed = ({ peds, selectedPed, handlevMenuPedImport }: vMenuPedProps) => {
  const { locales } = useNuiState();

  if (!locales) {
    return null;
  }

  return (
    <Section title="vMenu Ped import">
      <Item>
        Zde si můžete naimportovat libovolnou postavu z vMenu.<br />
        Tím však dojde k přepsání aktuální postavy!<br /><br />
        <SelectInput
          title="Ped"
          items={peds}
          defaultValue=""
          clientValue={selectedPed}
          onChange={value => handlevMenuPedImport(value, peds)}
        />
      </Item>
    </Section>
  );
};

export default VMenuPed;
